
.nav {
    background-color: rgb(100 214 124);
    padding: 15px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    /*height: 50px;*/
    align-items: center;
    width: 100%;
}

    @media (max-width: 431px) {
       .nav{
           flex-direction: column;
           width: 100%;


       }
    }

.item a {
    color: rgb(63, 51, 65);
    margin: 0 10px;
}

.item a.active {
    color: rgb(53, 6, 61);
    margin: 0 10px;
}